import cms from '@clients/cms.client';
import { AxiosError, AxiosResponse } from 'axios';
import lodash from 'lodash';
import { Dispatch, Store } from 'redux';
import _ from 'lodash';

export const GET_MAIN_ARTICLES = "CMS/GET_MAIN_ARTICLES";
export const GET_MAIN_ARTICLES_SUCCESS = "CMS/GET_MAIN_ARTICLES_SUCCESS";
export const GET_MAIN_ARTICLES_FAILED = "CMS/GET_MAIN_ARTICLES_FAILED";

export const GET_ARTICLE = "CMS/GET_ARTICLE";
export const GET_ARTICLE_SUCCESS = "CMS/GET_ARTICLE_SUCCESS";
export const GET_ARTICLE_FAILED = "CMS/GET_ARTICLE_FAILED";

export const GET_POPULAR_ARTICLES = "CMS/GET_POPULAR_ARTICLES";
export const GET_POPULAR_ARTICLES_SUCCESS = "CMS/GET_POPULAR_ARTICLES_SUCCESS";
export const GET_POPULAR_ARTICLES_FAILED = "CMS/GET_POPULAR_ARTICLES_FAILED";

export const getMainArticles: any = (payload = {
    searchText: undefined,
    page: 1,
    populate: '*',
    pagination: {
        pageSize: 6,
        page: 1,
        withCount: true,
    },
    sort: "createdAt:desc"
}) => (dispatch: any, getStore: any) => {
    dispatch({ type: GET_MAIN_ARTICLES, payload });
    return cms.get('/blogs', {
        params: {
            "pagination[pageSize]": payload?.pagination?.pageSize,
            "pagination[page]": payload?.pagination?.page,
            "pagination[withCount]": payload?.pagination?.withCount,
            populate: payload?.populate,
            sort: payload?.sort
        },
    }).then((response: AxiosResponse) => {
        dispatch({ type: GET_MAIN_ARTICLES_SUCCESS, payload: response.data });
        return response.data;
    }).catch((error: AxiosError) => {
        dispatch({ type: GET_MAIN_ARTICLES_FAILED, payload: error });
        return error.response?.data;
    })
}

export const getArticle = (payload = {
    path: undefined
}) => (dispatch: any, getStore: any) => {
    const pagination = {
        pageSize: 1,
        page: 1,
        withCount: false
    };
    dispatch({ type: GET_ARTICLE, payload });
    return cms.get(`/blogs/${payload?.path}`, {
        params: {
            "pagination[pageSize]": pagination?.pageSize,
            "pagination[page]": pagination?.page,
            "pagination[withCount]": pagination?.withCount,
            populate: "*",
        },
    }).then((response: AxiosResponse<any>) => {
        dispatch({
            type: GET_ARTICLE_SUCCESS, payload: {
                ...response.data,
                path: response?.data?.data?.attributes?.path
            }
        });
        return response.data;
    }).catch((error: AxiosError) => {
        dispatch({ type: GET_ARTICLE_FAILED, payload: error });
        return error.response?.data;
    })
}

export const getPopularArticles: any = (payload = {
    searchText: undefined,
    page: 1,
    populate: '*',
    pagination: {
        pageSize: 20,
        page: 1,
        withCount: true,
    },
    sort: "createdAt:desc"
}) => (dispatch: any, getStore: any) => {
    dispatch({ type: GET_POPULAR_ARTICLES, payload });
    return cms.get('/blogs', {
        params: {
            "pagination[pageSize]": payload?.pagination?.pageSize,
            "pagination[page]": payload?.pagination?.page,
            "pagination[withCount]": payload?.pagination?.withCount,
            populate: payload?.populate,
            sort: payload?.sort
        },
    }).then((response: AxiosResponse) => {
        dispatch({ type: GET_POPULAR_ARTICLES_SUCCESS, payload: response.data });
        return response.data;
    }).catch((error: AxiosError) => {
        dispatch({ type: GET_POPULAR_ARTICLES_FAILED, payload: error });
        return error.response?.data;
    })
}

const initialState = {
    cms: undefined,
    fetchingMainArticles: false,
    errorFetchingMainArticles: false,
    mainArticles: [],
    fetchingPopularArticles: false,
    errorFetchingPopularArticles: false,
    popularArticles: [],
    fetchingArticle: false,
    errorFetchingArticle: false,
    articles: {},
}

export default (state = initialState, action) => {
    switch (action.type) {
        case GET_MAIN_ARTICLES:
            return {
                ...state,
                fetchingMainArticles: true,
                errorFetchingMainArticles: false,
            }
        case GET_MAIN_ARTICLES_SUCCESS:
            return {
                ...state,
                fetchingMainArticles: false,
                errorFetchingMainArticles: false,
                mainArticles: _.uniqBy([...state?.mainArticles, ...action.payload?.data], "id")
            }
        case GET_MAIN_ARTICLES_FAILED:
            return {
                ...state,
                fetchingMainArticles: false,
                errorFetchingMainArticles: true,
                mainArticles: []
            }
        case GET_ARTICLE:
            return {
                ...state,
                fetchingArticle: true,
                errorFetchingArticle: false
            }
        case GET_ARTICLE_SUCCESS:
            return {
                ...state,
                fetchingArticle: false,
                errorFetchingArticle: false,
                articles: {
                    ...state.articles,
                    [action.payload?.path]: action.payload?.data
                }
            }
        case GET_ARTICLE_FAILED:
            return {
                ...state,
                fetchingArticle: false,
                errorFetchingArticle: true,
                articles: {
                    ...state.articles,
                    [action.payload?.path]: null
                }
            }
        case GET_POPULAR_ARTICLES:
            return {
                ...state,
                fetchingPopularArticles: true,
                errorFetchingPopularArticles: false,
            }
        case GET_POPULAR_ARTICLES_SUCCESS:
            return {
                ...state,
                fetchingPopularArticles: false,
                errorFetchingPopularArticles: false,
                popularArticles: _.uniqBy([...state?.popularArticles, ...action.payload?.data], "id")
            }
        case GET_POPULAR_ARTICLES_FAILED:
            return {
                ...state,
                fetchingPopularArticles: false,
                errorFetchingPopularArticles: true,
                popularArticles: []
            }
        default:
            return state;
    }
};
